/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect } from "react";
import "./index.scss";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

// actions
import {
  getDetailMyProjectApi,
} from "../../../../stores/reducers/projectSlice";

// ui
import { CRow, CCol, CContainer } from "@coreui/react";

// components
import BasicInfo from "./BasicInfo";
import Units from "./Units";
import LoadingContainer from "../../common/Loading";

const MyProjectContent = (props) => {
  const { id } = useParams();
  const dispatch = useDispatch();
  const { myProjectDetail, loading } = useSelector((state) => state.projects);

  useEffect(() => {
    if (!myProjectDetail) {
      dispatch(getDetailMyProjectApi(id));
    }
  }, [dispatch, id]);

  return (
    <div className="myProjectContent">
      {loading ? (
        <LoadingContainer />
      ) : (
        <CContainer>
          <div className="content">
            <CRow>
              <CCol md={6}>
                <div className="contentLeft">
                  <h1 className="title">
                    {(myProjectDetail && myProjectDetail.name) || ""}
                  </h1>
                </div>
              </CCol>
              <CCol md={6}>
                <Units
                  units={(myProjectDetail && myProjectDetail.units) || []}
                />
              </CCol>
            </CRow>

            <CRow className="mt-40">
              <CCol md={6}>
                <BasicInfo data={myProjectDetail} />
              </CCol>
              <CCol md={6}>
                <div className="infoImage">
                  <img
                    src="https://static.wixstatic.com/media/f4d468_2599098c0e4646babfc6e077abafc089~mv2.png/v1/fill/w_775,h_841,al_c,q_90/2925-sensitivity.webp"
                    alt=""
                  />
                </div>
              </CCol>
            </CRow>
          </div>
        </CContainer>
      )}
    </div>
  );
};

export default MyProjectContent;
